<template>
    <div id="distribuer_enseignant">
        <div class="container-fluid col-md-11">

            <h1>Distribuer à un enseignant</h1>
            <div class="row">
                <div class="col-md-4">
                    <div class="col-lg-12 big-bottom-buffer">
                        <div class="input-group">
                            <input type="text" class="form-control" placeholder="Rechercher un enseignant...">
                            <span class="input-group-btn">
                                <button class="btn" type="submit" id="search-btn"><i class="fa fa-search"></i></button>
                            </span>
                        </div>
                    </div>
                    <div class="row justify-content-md-center">                
                        <div class="row col-md-12">
                            <select class="form-control col-md bottom-buffer">
                                <option>-- Sélectionner une fourniture --</option>
                                <option>TGQ</option>
                                <option>DEE</option>
                                <option>Suisse mode d'emploi</option>
                                <option>CO</option>
                                <option>CCS</option>
                            </select>
                        </div>
                    </div>
                    
                    <button class="btn btn-primary bottom-buffer">Ajouter la fourniture sélectionnée</button>
                </div>

                <div class="col-md-8">
                    <h4>Nom de l'enseignant</h4>
                    <div class="table-responsive">
                        <table class="table table-striped">
                            <thead class="thead-dark">
                                <tr>
                                    <th scope="col">Désignation</th>
                                    <th scope="col" width="200">Prix (CHF)</th>
                                    <th scope="col" width="200">Quantité</th>
                                    <th scope="col">Retirer la fourniture</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="fourniture in fournitures" v-bind:key="fourniture.index">
                                    <td scope="row" id="col_fourniture">
                                        {{fourniture.nom}}
                                    </td>
                                    <td>
                                        {{fourniture.prix}}
                                    </td>
                                    <td>
                                        <input type="text" class="form-control">
                                    </td>
                                    <td>
                                        <button class="btn btn-outline-danger">
                                            Retirer <i class="fa fa-times"></i>
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>

                    <button type="button" class="btn btn-primary">Annuler</button>

                    <button type="button" class="btn btn-primary">Distribuer</button>
                </div>
            </div>


        </div>


        

    </div>
    
</template>

<script>
import axios from 'axios';
export default {
    name: 'distribuer_enseignant',
    data() {
        return {
            enseignants: [],
            fournitures: [],
            errors: []
        }
    },
    created() {
        // Récupération des élèves
        axios.get(`http://localhost:3000/enseignant`)
        .then(response => {
            // JSON responses are automatically parsed.
            this.enseignants = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
        
        // Récupération des fournitures
        axios.get(`http://localhost:3000/fourniture`)
        .then(response => {
            // JSON responses are automatically parsed.
            this.fournitures = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
    }

}

</script>




<!-- styling for the component -->
<style scoped>
table {
    text-align: center;
    max-width: 1000px;
}

.big-bottom-buffer {
    margin-bottom: 50px;
}
</style>


